<template>
  <v-app>
    <v-main>
      <!-- Centered Title -->
      <v-row class="d-flex justify-center">
        <v-col cols="12" class="text-center">
          <h1 class="quiz-title">Telugu Jeopardy Game</h1>
        </v-col>
      </v-row>
      <v-container>
        <v-row class="align-start">
          <!-- Category Selection Dropdown -->
          <v-col cols="12" md="6" >
            <v-select
              v-model="selectedCategories"
              :items="categories"
              label="Select Categories"
              multiple
              :disabled="gameStarted"
              style="background-color: orange; height: 55px" 
            ></v-select>
            <!-- Instruction Text Below the Dropdown -->
            <p style="color: blue; margin-top: 15px;">(Please select one to five categories and then click "Start Game.")</p>
          </v-col>

          <!-- Start and Reset Game Buttons -->
          <v-col cols="auto">
            <v-btn class="start-game-btn" :disabled="gameStarted" @click="handleStartGame" style="width: 100px; height: 50px;" color="green">
              START
            </v-btn>
            <v-btn @click="resetGame" style="width: 100px; height: 50px; margin-left: 10px;" color="blue darken-4">
              RESET
            </v-btn>
          </v-col>
        </v-row>

        <!-- Error message if there is any -->
        <p v-if="errorMessage" style="color: red;font-size: 30px;" class="blinking-text">{{ errorMessage }}</p>

        <!-- Centered GameBoard with Left Shift -->
        <v-row class="d-flex justify-start mt-4">
          <v-col cols="12" md="8" class="text-center" style="margin-left: 0px;">
            <GameBoard
              v-if="gameStarted"
              :categories="selectedCategories"
              :openedQuestions="openedQuestions"
              :displayedQuestions="displayedQuestions"
              @ask-question="handleQuestion"
            ></GameBoard>
          </v-col>
        </v-row>

        <!-- Player Scores Section -->
        <v-row class="d-flex justify-start mt-4">
          <!-- Score Table - Display only when the game starts -->
          <v-col cols="12" md="8" class="text-center" style="margin-left: 0px;">
            <div v-if="gameStarted" class="player-scores">
              <h3>Player Scores</h3>
              <table class="score-table">
                <tr>
                  <th :class="currentPlayer === 0 ? 'active-player' : 'inactive-player'">Player 1</th>
                  <th :class="currentPlayer === 1 ? 'active-player' : 'inactive-player'">Player 2</th>
                </tr>
                <tr>
                  <td>{{ scores[0] }}</td>
                  <td>{{ scores[1] }}</td>
                </tr>
              </table>
            </div>
          </v-col>
        </v-row>

        <!-- Question Modal -->
        <v-dialog v-model="showModal" persistent max-width="600px" style="top: -50px;">
          <v-card>
            <!-- Ensure the text wraps within the available space -->
            <v-card-title style="white-space: normal; word-wrap: break-word;">
              {{ currentQuestion.question }}
            </v-card-title>

            <v-card-text>
              <p>Points: {{ currentQuestion.points }}</p>

              <!-- Word-wrap answer if it is shown -->
              <p v-if="showAnswer" style="white-space: normal; word-wrap: break-word;">
                Answer: {{ currentQuestion.answer }}
              </p>
            </v-card-text>

            <v-card-actions>
              <v-btn color="blue darken-1" text @click="revealAnswer">Show Answer</v-btn>
              <v-btn color="green darken-1" text v-if="showAnswer" @click="submitAnswer(true)">Correct</v-btn>
              <v-btn color="red darken-1" text v-if="showAnswer" @click="submitAnswer(false)">Incorrect</v-btn>
              <v-btn color="orange darken-1" text @click="skipQuestion">Skip</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

        <!-- Winner Announcement Modal -->
        <v-dialog v-model="showWinnerModal" persistent max-width="600px" class="winner-modal">
          <v-card>
            <v-card-title style="font-weight: bold; font-size: 24px; text-align: center;">Game Over</v-card-title>
            <v-card-text style="text-align: center; font-size: 18px;">
              <!-- Bold and dark green winnerMessage -->
              <p style="margin-bottom: 10px; font-weight: bold; color: darkgreen;">{{ winnerMessage }}</p>
              
              <!-- Styled Final Scores section -->
              <p style="font-weight: bold; margin-bottom: 5px;">Final Scores:</p>
              <ul style="list-style: none; padding: 0; font-size: 18px; margin-bottom: 5px;">
                <li style="margin-bottom: 5px;">üèÜ Player 1: <strong>{{ scores[0] }}</strong></li>
                <li>üèÜ Player 2: <strong>{{ scores[1] }}</strong></li>
              </ul>
            </v-card-text>

            <v-card-actions style="justify-content: center; margin-top: -30px;">
              <!-- Close button with increased size -->
              <v-btn color="blue darken-1" text @click="showWinnerModal = false" style="font-size: 18px; bold; padding: 12px 24px;">
                Close
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import Papa from 'papaparse';
import GameBoard from './components/GameBoard.vue';

export default {
  name: 'App',
  components: {
    GameBoard,
  },
  data() {
    return {
      categories: [],
      selectedCategories: [],
      questions: {},
      showModal: false,
      showWinnerModal: false,  // Winner Modal visibility
      currentQuestion: {},  // Hold the current question data
      scores: [0, 0],
      currentPlayer: 0,
      showAnswer: false,
      openedQuestions: [],
      gameStarted: false,
      totalSelectedQuestions: 0,  
      displayedQuestions: {},  // Hold random questions
      questionsAnswered: 0,  // Track the number of questions answered
      totalQuestions: 25,  // Total questions in the game
      winnerMessage: '',  // Message to display the winner
      errorMessage: ''  // Error message for validation
    };
  },
  methods: {
    loadQuestions() {
      const csvFile = process.env.NODE_ENV === 'production' ? '/jeopardy-game/questions.csv' : '/questions.csv';
      Papa.parse(csvFile, {
        header: true,
        download: true,
        complete: (result) => {
          this.processQuestions(result.data);
        }
      });
    },
    processQuestions(data) {
      const categories = new Set();
      const questions = {};

      data.forEach((row) => {
        const { Category, Question, Answer, Marks } = row;

        if (Category && Question && Answer && Marks) {
          categories.add(Category);

          if (!questions[Category]) {
            questions[Category] = [];
          }

          questions[Category].push({
            question: Question,
            answer: Answer,
            points: parseInt(Marks, 10),
          });
        }
      });

      this.categories = Array.from(categories);
      this.questions = questions;
    },
    shuffle(array) {
      let currentIndex = array.length, randomIndex;
      while (currentIndex !== 0) {
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;
        [array[currentIndex], array[randomIndex]] = [array[randomIndex], array[currentIndex]];
      }
      return array;
    },
    handleStartGame() {
      // Check if no categories are selected or more than 5 categories are selected
      if (this.selectedCategories.length === 0) {
        this.errorMessage = "Please select at least one category.";
        return;
      }
      if (this.selectedCategories.length > 5) {
        this.errorMessage = "Please select only a maximum of 5 categories.";
        return;
      }

      // Clear error message and proceed
      this.errorMessage = "";
      this.gameStarted = true;

      // Determine the max questions per category based on the number of selected categories
      let maxQuestionsPerCategory = 25;  // Default for one category
      const numSelectedCategories = this.selectedCategories.length;

      if (numSelectedCategories === 2) {
        maxQuestionsPerCategory = 20;
      } else if (numSelectedCategories === 3) {
        maxQuestionsPerCategory = 10;
      } else if (numSelectedCategories >= 4) {
        maxQuestionsPerCategory = 5;
      }

      // Sort selectedCategories based on available question count (ascending)
      const sortedCategories = [...this.selectedCategories].sort((a, b) => {
        const availableQuestionsA = this.questions[a] ? this.questions[a].length : 0;
        const availableQuestionsB = this.questions[b] ? this.questions[b].length : 0;
        return availableQuestionsA - availableQuestionsB;
      });

      const selectedQuestions = {};
      let totalSelectedQuestions = 0;
      let remainingQuestions = this.totalQuestions; // Max of 25 questions
      let remainingSelectedCategories = sortedCategories.length;
      let questionsPerCategory = Math.floor(remainingQuestions / remainingSelectedCategories); // Evenly distribute

      sortedCategories.forEach((category) => {
        const shuffledQuestions = this.shuffle([...this.questions[category]]); // Shuffle questions
        
        // Limit questions to the nearest multiple of 5 and within maxQuestionsPerCategory
        const numQuestionsInCategory = Math.min(questionsPerCategory, maxQuestionsPerCategory);
        const adjustedQuestionsCount = Math.floor(numQuestionsInCategory / 5) * 5;  // Multiple of 5

        selectedQuestions[category] = shuffledQuestions.slice(0, adjustedQuestionsCount);
        selectedQuestions[category].sort((a, b) => a.points - b.points);
        totalSelectedQuestions += selectedQuestions[category].length;
        remainingQuestions -= selectedQuestions[category].length;
        remainingSelectedCategories -= 1;
        questionsPerCategory = Math.floor(remainingQuestions / remainingSelectedCategories);
      });

      this.totalSelectedQuestions = totalSelectedQuestions;
      this.displayedQuestions = selectedQuestions;
      this.questionsAnswered = 0;  // Reset questions answered
    },
    handleQuestion({ category, index }) {
      const selectedQuestion = this.displayedQuestions[category][index];
      if (selectedQuestion) {
        this.currentQuestion = selectedQuestion;
        this.showModal = true;
        this.showAnswer = false;
        this.openedQuestions.push({ category, index });
      }
    },
    revealAnswer() {
      this.showAnswer = true;
    },
    submitAnswer(isCorrect) {
      if (isCorrect) {
        this.scores[this.currentPlayer] += this.currentQuestion.points;
      } else {
        this.scores[this.currentPlayer] -= this.currentQuestion.points;
      }
      this.questionsAnswered++;
      this.closeModal();
      this.checkGameOver();  // Check after closing modal
      this.switchPlayer();
    },
    skipQuestion() {
      this.questionsAnswered++;
      this.closeModal();  // Close the modal without changing score
      this.checkGameOver();  // Check after closing modal
      this.switchPlayer();  // Switch to the next player
    },
    // Method to handle the Escape key press
    handleEscapeKey(event) {
      if (event.key === "Escape" && this.showModal) {
        this.skipQuestion();  // Trigger skip logic when Escape is pressed
      }
    },
    closeModal() {
      this.showModal = false;
      this.showAnswer = false;
    },
    switchPlayer() {
      this.currentPlayer = (this.currentPlayer + 1) % this.scores.length;
    },
    checkGameOver() {
      if (this.questionsAnswered >= this.totalSelectedQuestions) {
        this.showWinner();
      }
    },
    showWinner() {
      if (this.scores[0] > this.scores[1]) {
        this.winnerMessage = 'Player 1 is the winner!';
      } else if (this.scores[1] > this.scores[0]) {
        this.winnerMessage = 'Player 2 is the winner!';
      } else {
        this.winnerMessage = 'It\'s a tie!';
      }
      this.showWinnerModal = true;  // Show the winner modal immediately
    },
    resetGame() {
      this.scores = [0, 0];
      this.openedQuestions = [];
      this.questionsAnswered = 0;
      this.gameStarted = false;
      this.showWinnerModal = false;
      this.selectedCategories = []; // Clear selected categories
      this.displayedQuestions = {};  // Clear questions from the board
      this.errorMessage = "";
    }
  },
  mounted() {
    this.loadQuestions();
    // Add a keydown listener when the component is mounted
    window.addEventListener("keydown", this.handleEscapeKey);
  },
  beforeUnmount() {
    // Remove the keydown listener when the component is destroyed
    window.removeEventListener("keydown", this.handleEscapeKey);
  }
};
</script>

