<template>
  <div class="game-board">
    <table>
      <thead>
        <tr>
          <!-- Loop over columns to display the correct category header -->
          <th v-for="(column, colIndex) in distributedColumns" :key="colIndex">
            {{ column.category }}
          </th>
        </tr>
      </thead>
      <tbody>
        <!-- Loop through rows to display 5 rows of questions -->
        <tr v-for="rowIndex in 5" :key="rowIndex">
          <td v-for="(column, colIndex) in distributedColumns" :key="colIndex">
            <v-btn
              :class="{ 'answered': isOpened(column.category, getQuestionIndex(column, rowIndex)) }"
              :style="isOpened(column.category, getQuestionIndex(column, rowIndex)) 
                      ? { backgroundColor: 'grey', color: 'white' } 
                      : { backgroundColor: 'lightblue', color: 'purple' }"
              v-if="getQuestionForCell(column, rowIndex)"
              @click="$emit('ask-question', { category: column.category, index: getQuestionIndex(column, rowIndex) })"
              style="width: 120px; height: 30px; margin: -5px;" 
            >
              {{ getQuestionForCell(column, rowIndex).points }} POINTS
            </v-btn>
          </td>
        </tr>
      </tbody>
    </table>

  </div>
</template>

<script>
export default {
  props: {
    categories: Array, // List of selected categories
    displayedQuestions: Object, // Questions by category
    openedQuestions: Array, // List of opened questions
  },
  computed: {
    distributedColumns() {
      // Create an array to store the distributed columns
      const columns = [];

      this.categories.forEach((category) => {
        const categoryQuestions = this.displayedQuestions[category] || [];

        // Loop over category questions in batches of 5 and assign to columns
        for (let i = 0; i < Math.ceil(categoryQuestions.length / 5); i++) {
          columns.push({
            category: category,
            startIndex: i * 5, // Start index for the batch of questions for this column
          });
        }
      });

      return columns;
    }
  },
  methods: {
    // Helper function to get the correct question for the current column and row
    getQuestionForCell(column, rowIndex) {
      const category = column.category;
      const questionIndex = column.startIndex + (rowIndex - 1);
      if (this.displayedQuestions[category] && this.displayedQuestions[category][questionIndex]) {
        return this.displayedQuestions[category][questionIndex];
      }
      return null;
    },

    // Helper function to get the correct question index for openedQuestions
    getQuestionIndex(column, rowIndex) {
      return column.startIndex + (rowIndex - 1);
    },

    // Check if a question has been opened or answered
    isOpened(category, index) {
      return this.openedQuestions.some(q => q.category === category && q.index === index);
    }
  }
};
</script>

<style scoped>
.game-board {
  width: 100%;
  max-width: 800px;
  margin: auto;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  text-align: center;
  padding: 10px;
}

v-btn {
  margin: 5px !important;
}

.greyed-out {
  background-color: lightgrey !important;
  pointer-events: none;
}

/* Add a total questions display */
.total-questions {
  margin-top: 20px;
  text-align: center;
}

.total-questions p {
  font-size: 16px;
  margin: 5px 0;
}

.answered {
  background-color: grey !important;
  color: white !important;
  pointer-events: none;
}
</style>